// 然后定义路由(routes)，components还可以是Vue.extend()创建的
import StreamVideoIndex from './components/StreamVideoIndex';
import StreamIndex from './components/StreamIndex';
import StreamArtIndex from './components/StreamArtIndex';
const routes = [
    {
        path: '/',
        component: StreamIndex
    },
    { 
        path: '/video', 
        component: StreamVideoIndex 
    },
    {
        path: '/article',
        component: StreamArtIndex
    }
];
export default routes;


