<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import StreamIndex from './components/StreamIndex'
import style from '../static/common.css';
import fontstyle from '../static/font/font.css';
export default {
    name: 'app',
    components: {
      StreamIndex
    },
    mounted() {
      // 数据上报
      window._rlog = window._rlog || [];
      // 指定 product id
      window._rlog.push(["_setAccount", "ead_stream"]);
      // 设置为 false，将不会自动发送 PV
      window._rlog.push(["_setAutoPageview", false]);
    }
}
</script>

<style>
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html,body {
    font-family: "Helvetica Neue",Helvetica,sans-serif;
    width: 100%;
    height: 100%;
}
#app {
    // -webkit-font-smoothing: antialiased;
    // -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    width: 100%;
    height: 100%;
}
.lazy-img {
    width: 100%;
}
</style>



