<template>
    <div class='video-content-container'>
        <div class='video-content-title'>
            【双语】梦龙-无论付出什么代价
        </div>
        <StreamVideoPlayer  
            @callback="getVideo"
            :src='video_url'/>
        <div class='video-content-videomsg'>
            视频时长: {{video.duration}}
        </div>
        <div class='video-content-extramsg'>
            如果你也喜欢这个小视频，欢迎分享。让更多的朋友看到哦～
        </div>
    </div>
</template>
<script>
import StreamVideoPlayer from './StreamVideoPlayer';
export default {
    computed: {
        videoid: function() {
            return this.$route.query.id;
        }
    },
    components: {
        StreamVideoPlayer
    },
    data () {
		return {
            video: {
                duration: `03' 40"`
            },
            video_url: 'http://video.pearvideo.com/mp4/short/20171216/cont-1226543-11276974-hd.mp4'
		}
	},
    methods: {
        // 获得视频信息
        getVideo: function(msg) {
            this.video.duration = msg;
        }
    },
    mounted() {

    }
}
</script>
<style scoped>
    .video-content-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: .4rem .32rem;
        font-size: .4rem;
    }
    .video-content-title {
        font-size: .533333rem;
        font-weight: bold;
        padding-bottom: .426667rem;
        width: 100%;
        border-bottom: 1px #aaa dashed;
    }
    .video-content-wrapper {
        width: 100%;    
        height: 5.333333rem;
        margin: .426667rem 0;
    }
    .video-content-wrapper video {
        width: 100%;
        height: 100%;
        object-fit:fill;
    }
    .video-content-videomsg {
        text-align: left;
        margin-bottom: .32rem;
        color: #aaa;
    }
    .video-content-extramsg {
        text-align: left;
        color: #9b9ea3;
        line-height: .666667rem;
    }
</style>


