<template>
    <div class='stream-content-container' @click='bind_jump_article'>
        <div class='stream-content-img'>
            <img :src="item.image">
        </div>
        <div class='stream-content-desc'>
            <div class='stream-content-title'>
                {{item.title}}
            </div>
            <div class='stream-content-extramsg'>
                <div class='stream-content-pv'
                    v-show='!pure'>
                    {{item.visitnum}}人已浏览
                </div>
                <div class='stream-content-date'
                    v-show='!pure'>
                    {{`${content_date}`.slice(5, 10)}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import StreamVideoIndex from './StreamVideoIndex';
import util from '../util/util.js';
export default {
    props: ['item', 'date', 'pure', 'norlog', 'firstlog'],
	data () {
		return {
            content_date: ''
            // 第一次记录的标志
		}
	},
    methods: {
        bind_jump_article: function() {
            console.log(this.item.title);
            window._rlog.push(["_addPost" , "infoid" , `${this.item.id}`]);
            window._rlog.push(["_addPost" , "style" , `${this.item.style}`]);
            window._rlog.push(["_addPost" , "url" , `${this.item.url}`]);
            window._rlog.push(["_addPost" , "date" , `${util.formatTime(new Date)}`]);
            window._rlog.push(["_trackEvent", `index_detail`]);
            
            let urlId = this._bind_get_urlId(this.item.url);
            // 分为视频页和文章页
            this.$router.push({ path: `/article?urlId=${urlId}&title=${this.item.title}` })
        },
        _bind_get_urlId: function(url) {
            if (!url) return ``;
            let urlList = url.split('/');
            let len = urlList.length;
            return urlList[len-1];
        }
    },
	mounted() {
        this.content_date = util.formatTime(new Date(this.item.date));
        if (this.item && !this.norlog) {
            console.log(111)
            // 增加一条自定义 'stream_card_pv'
            window._rlog.push(["_addPost" , "infoid" , `${this.item.id}`]);
            window._rlog.push(["_addPost" , "style" , `${this.item.style}`]);
            window._rlog.push(["_addPost" , "url" , `${this.item.url}`]);
            window._rlog.push(["_addPost" , "date" , `${util.formatTime(new Date)}`]);
            window._rlog.push(["_trackEvent", `infoline_shown`]);
        }
	},
    updated() {
        if (!this.firstlog) {
            if (this.item && !this.norlog) {
            console.log(2222)
                window._rlog.push(["_addPost" , "infoid" , `${this.item.id}`]);
                window._rlog.push(["_addPost" , "style" , `${this.item.style}`]);
                window._rlog.push(["_addPost" , "url" , `${this.item.url}`]);
                window._rlog.push(["_addPost" , "date" , `${util.formatTime(new Date)}`]);
                window._rlog.push(["_trackEvent", `infoline_shown`]);
            }
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .stream-content-container {
        display: flex;
        // padding: .426667rem 0 .426667rem 0;
        padding: .266667rem 0 .16rem 0;
    }
    .stream-content-img {
        // width: 2.266667rem;
        width: 1.133333rem;
        // height: 1.733333rem;
        height: .866667rem;
        // border-radius: .106667rem;
        border-radius: .053333rem;
        overflow: hidden;
    }
    .stream-content-img img{
        width: 100%;
        height: 100%;
        vertical-align: top;
    }
    .stream-content-desc {
        flex: 1;
        display: flex;
        flex-direction: column;
        // padding: .053333rem 0 .08rem .373333rem;
        padding: .026667rem 0 .04rem .186667rem;
        color: #000000;
    }
    .stream-content-title {
        text-align: left;
        // font-size: .373333rem;
        font-size: .213333rem;
        // line-height: .493333rem;
        line-height: .24rem;
        color: #333A42;
        word-wrap: break-word;  
        word-break: break-all; 
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
    }
    .stream-content-extramsg {
        flex: 1;
        color: #b8bcc2;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        // font-size: .293333rem;
        font-size: .16rem;
        line-height: .16rem;
    }
</style>


