<template>
    <div class='audio-content-wrapper'>
        <div class='audio-content-album' >
            <div class='audio-content-iconlist' 
                @touchstart='bind_toogle_play'>
                <i class="icon iconfont icon-b"
                    v-show="isplay"></i>
                <i class="icon iconfont icon-bofang"
                    v-show="!isplay"></i>
            </div>
            <div class='audio-content-toogle'
                :style="backgroundImg"></div>
        </div>
        <div class='audio-content-desc'>
            <div class='audio-content-title'>
                {{title}}
            </div>
            <div class='audio-content-msg'>
                <div class='audio-content-barwrapper' 
                    ref='processbarwrapper'
                    @click='bind_process_jump'>
                    <audio ref='audio' 
                        :src='url'>
                    </audio>
                    <div 
                        class='audio-content-processbar' 
                        ref='processbar'>
                        <span class='audio-content-dotbar-wrapper'>
                            <span class='audio-content-dotbar'
                                @touchstart='bind_process_start'
                                @touchmove='bind_process_move'
                                @touchend='bind_process_end'>
                            </span>
                        </span>
                    </div>
                </div>
                <div class='audio-content-processmsg'>
                    {{curTime}}/{{totalTime}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    computed: {
        backgroundImg: function() {
            return {
                backgroundImage: `url(${this.pic})`
            }
        }
    },
    props: {
        url: {
            type: String,
            default() {
                return '';
            }
        },
        title: {
            type: String,
            default() {
                return '';
            }
        },
        pic: {
            type: String,
            default() {
                return '';
            }
        }
    },
    data: function() {
        return {
            // 正在播放的状态
            isplay: false,
            // 目前时间
            curTime: '00:00',
            // 总时间
            totalTime: '00:00',
            // 是否已结束播放
            ended: false
        }
    },
    methods: {
        // 切换播放状态
        bind_toogle_play() {
            if (this.isplay) {
                this.audio.pause();
            } else {
                this.audio.play();
            }
            this.isplay = !this.isplay;
        },
        // 拖动进度条逻辑
        bind_process_start: function(e) {
            // 初始化位置
            this.process_start = e.touches[0].clientX;
        },
        bind_process_move: function(e) {
            // 获得移动距离
            this.move = e.touches[0].clientX - this.process_start;
            // 算出当前偏移量和总长的比例
            const cur_proportion = (this.processbar_width + this.move) / this.processwrapper_width;
            console.log(this.processbar_width, this.move);
            // 如果比例超出界限，直接return
            if (cur_proportion > 1 || cur_proportion < 0) {
                return;
            } else {
                this.processbar.style.width = `${cur_proportion*100}%`;
            }
        },
        bind_process_end: function() {
            // 更新每次偏移之后的初始位置
            let cur_move = this.processbar_width + this.move;
            let proportion = cur_move/this.processwrapper_width;
            // 计算边界情况
            if (cur_move > this.processwrapper_width) {
                // 如果改变的幅度过大，从99%播放
                proportion = 0.99;
            } else if (cur_move < 0) {
                proportion = 0;
            }
            this.processbar.style.width = `${proportion*100}%`;
            this.processbar_width = this.processwrapper_width * proportion;
            // 初始化
            this._bind_process_jump(proportion);
            this.move = 0;
            this.process_start = 0;
        },
         // 点击进度条跳转
        bind_process_jump: function(e) {
            // 如果视频暂停的时候点击跳转，clear掉原来的计时器
            if (!this.audio.paused) {
                clearInterval(this.start_timer);
            }
            // 计算跳转的偏移百分比
            const move = e.clientX - this.processwrapper_offsetLeft;
            let proportion = move/this.processwrapper_width;
            if (move > this.processwrapper_width) {
                // 如果改变的幅度过大，从99%播放
                proportion = 0.99;
            } else if (move < 0) {
                // 如果改变小于播放起始时间，直接return
                return;
            }
            this.processbar_width = this.processwrapper_width * proportion;
            this.processbar.style.width = `${proportion*100}%`;
            // 改变当前的播放时间
            this._bind_process_jump(proportion)
        },
        // 改变当前播放时间和状态
        _bind_process_jump: function(ppt) {
            console.log('current ppt:', ppt);
            this.ppt = ppt*100;
            this.audio.currentTime = this.audio.duration*ppt;
            this.audio.play();
            this.isplay = true;
        },
        // 根据秒数计算分秒显示
        _bind_cacal_time: function(sec) {
            let hour = `0${parseInt(sec/3600)}`.slice(-2);
            let min = `0${parseInt(sec/60)}`.slice(-2);
            let second = `0${parseInt(sec%60)}`.slice(-2);
            if (hour == '00') {
                return `${min}:${second}`;
            } else {
                return `${hour}:${min}:${second}`;
            }
        },
        // 播放逻辑
        bind_process_play: function(proportion) {
            let per_curbar_width = this.processwrapper_width/100;
            // 如果播放结束，重新播放
            if (this.ended) {
                // 重置进度条
                this.ppt = 0;
                this.processbar_width = 0;
                this.ended = false;
            } else if (this.processbar_width >= this.processwrapper_width) {
                this.audio.pause();
            }
            this.start_timer = setInterval(() => {
                !this.ppt && (this.ppt = 0);
                this.ppt = this.ppt + proportion;
                this.processbar_width += per_curbar_width;
                console.log(`${this.ppt}%`);
                this.processbar.style.width = `${this.ppt}%`;
                this.curTime = this._bind_cacal_time(this.audio.currentTime);
            }, 1000)   
        },
        // 暂停逻辑
        bind_process_pause: function() {
            if (this.audio.ended) {
                this.processbar_width = this.processwrapper_width;
                this.ppt = 100;
                this.curTime = this._bind_cacal_time(this.audio.duration);
                this.isplay = false;
                // 手动设置状态 重新播放的判断
                this.ended = true;
            }
            clearInterval(this.start_timer);
        },
    },
    mounted: function() {
        // 获取audio元素
        this.audio = this.$refs.audio;
        // 目前进度条长度
        this.processbar_width = 0;
        // 获取当前进度条元素
        this.processbar = this.$refs.processbar;
        // 获取进度条元素
        this.processbarwrapper = this.$refs.processbarwrapper;
        // 滚动条的长度
        this.processwrapper_width = this.processbarwrapper.getBoundingClientRect().width;
        // 滚动条的偏移量
        this.processwrapper_offsetLeft = this.processbarwrapper.getBoundingClientRect().left;
        let that = this;
        // 监听媒体的事件
        this.audio.onplaying = function() {
            console.log('playing')
            this.ppt = that.audio.currentTime/that.audio.duration * 100;
            that.bind_process_play(100/that.audio.duration);
        }
        this.audio.onpause = function() {
            console.log('pause')
            that.bind_process_pause();
        }
        this.audio.oncanplay = function() {
            console.log('can play')
            // 总进度时间
            that.totalTime = that._bind_cacal_time(that.audio.duration);
        }
        this.audio.onwaiting = function() {
            console.log("can't play");
            that.start_timer && clearInterval(that.start_timer);
        }
    }
}
</script>
<style scoped>
    .audio-content-wrapper {  
        position: relative;
        height: 1rem;
        display: flex;
        align-items: center;
        padding: .16rem;
        background: #FAFAFA;
        border: 1px solid #EBEBEB;
        border-radius: .033333rem;
        box-sizing: border-box;
        // margin: .266667rem 0;
        font-size: .213333rem;
        -webkit-user-select:none;
        -moz-user-select:none;
        -ms-user-select:none;
        user-select:none;
        -webkit-tap-highlight-color: transparent;
         -webkit-touch-callout: none;
        cursor: none;
    }
    .audio-content-album {
        width: .666667rem;
        background: #fff;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: .033333rem;
        position: relative;
    }
    .audio-content-album .audio-content-iconlist {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 999;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
    }
    .audio-content-album .audio-content-iconlist i {
        font-size: .2rem;
        font-weight: bold;
        z-index: 1;
    }
    .audio-content-toogle {
        border-radius: .033333rem;
        width: .693333rem;
        height: 100%;
        background-size: 100% 100%;
        // opacity: 0.7;
    }
    .audio-content-desc {
        // margin-left: .4rem;
        margin-left: .213333rem;
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
    .audio-content-title {
        font-size: .173333rem;
        font-family: 'HYQiHei-EES';
        padding: .1rem 0;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        width: 2.666667rem;
        line-height: .226667rem;
        color: #333;
    }
    .audio-content-msg {
        display: flex;
        align-items: center;
    }
    .audio-content-barwrapper {
        width: 2.213333rem;
        height: .026667rem;
        border-radius: .033333rem;
        background: #EBEBEB;
        position: relative;
        margin-left: 0.16rem;
    }
    .audio-content-processbar {
        position: absolute;
        left: 0;
        top: 0;
        width: 0%;
        height: 100%;
        background: #D91019;
    }
    .audio-content-dotbar-wrapper {
        width: .16rem;
        height: .16rem;
        // border: .066667rem solid #D91019;
        border: .033333rem solid #D91019;
        border-radius: 50%;
        position: absolute;
        right: 0;
        background: #fff;
        top: -.07rem;
    }
    .audio-content-dotbar {
        width: .24rem;
        height: .24rem;
        border-radius: 50%;
        position: absolute;
        top: -.073333rem;
        left: -.073333rem;
        // background: #fff;
    }
    .audio-content-processmsg {
        font-family: "Helvetica Neue",Helvetica,sans-serif;
        font-size: .11rem;
        letter-spacing: 0;
        margin-left: .213333rem;
        color: #999;
        line-height: .146667rem;
    }
</style>


