<template>
	<div class='stream-container'
		@touchstart='bind_update_start'
		@touchmove='bind_update_move'
		@touchend='bind_update_end'
		:style='move_style'
		v-infinite-scroll="_bind_load_more" 
		infinite-scroll-disabled="busy" 
		infinite-scroll-distance="10"
		infinite-scroll-immediate-check="false">
		<StreamLoading v-if='need_update'/>
		<div class='stream-loadend' v-if='loadend'>
			没有内容了~
		</div>
		<div v-for='(content_item, index) in stream_list'>
			<div class='stream-ad'
				v-if='bind_detect(index)'>
				<StreamAdItem />
			</div>
			<div class='stream-content'
				v-if='!bind_detect(index)'>
				<StreamContentItem 
				:item='content_item'
				:date='stream_date'
				:firstlog='first_log'/>
			</div>
		</div>
		<div class='stream-loadend' v-if='loadend'>
			没有内容了~
		</div>
		<StreamLoading v-if='busy && !loadend'/>
	</div>
</template>

<script>
	import StreamContentItem from './StreamContentItem';
	import StreamAdItem from './StreamAdItem';
	import StreamLoading from './StreamLoading';
	import api from '../service/apihelper.js';
	import util from '../util/util.js';
	export default {
		components: {
			StreamContentItem,
			StreamAdItem,
			StreamLoading
		},
		name: 'Stream',
		data () {
			return {
				// 第一次记录数据
				first_log: true,
				// 禁止执行下拉回调	
				no_pull: false,
				// 是否加载完毕
				loadend: false,
				// 下拉刷新的锁 当触发条件才可以解锁
				pull_lock: true,
				// 出发刷新的拉伸度
				pull_limit: 150,
				// 出发加载loading的flag
				need_update: false,
				// 广告输出的位置
				ad_position: 6,
				// 下拉style
				move_style: { transform: 'translate3d(0, 0, 0)' },
				// 出发加载flag
				busy: false,
				stream_date: '',
				stream_params: '',
				stream_list: [],
				stream_start: 0,
				// stream 每次增加的数目
				stream_start_addnum: 16
			}
		},
		methods: {
			bind_detect: function(index) {
				let cur_idx = index + 1;
				return cur_idx%this.ad_position == 0;
			},
			bind_update_start: function(e) {
				const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop == 0 && !this.need_update) {
					this.pull_lock = false;
					this.update_start_pos = e.touches[0].clientY;
				}
			},
			bind_update_move: function(e) {
				// 当下拉锁住了 才可以开始下拉刷新
				if (!this.pull_lock) {
					let move = (e.touches[0].clientY - this.update_start_pos);
					// 只有往下拉 才会开始下拉刷新
					if (move > 0) {
						// 最多能拉到100
						move = move > this.pull_limit ? this.pull_limit: move;
						this.move_style.transform = `translate3d(0px, ${move}px, 0px)`;
					}
				}
			},
			bind_update_end: function(e) {
				if (!this.pull_lock) {
					let update_end_pos = e.changedTouches[0].clientY;
					let move = update_end_pos - this.update_start_pos;
					this.move_style.transform = `translate3d(0px, 0px, 0px)`;
					// 满足拉动一定长度才进行刷新
					if (move > this.pull_limit) {
						this.pull_lock = true;
						this._bind_pull_more();
					}
				}
			},
			// 下拉刷新
			_bind_pull_more: function() {
				this.need_update = true;
				this.stream_start = this.stream_start + this.stream_start_addnum;
				this.stream_params = this._bind_stream_params(this.stream_date, this.stream_start);
				api.get_stream(this, this.stream_params).then((result) => {
					// 下拉刷新让所有的item都刷新一遍
					this.need_update = false;
					if (!result.cards || result.cards.lenght < 16) {
						this.loadend = true;
						setTimeout(() => {
							this.loadend = false;
						}, 1500)
					} else { 
						this.stream_list = result['cards'].slice(0, 16);
						this.first_log = false;
					}
				})
			},
			// 上拉加载
			_bind_load_more: function() {
				// 不允许继续加载
				this.busy = true;
				this.stream_start = this.stream_start + this.stream_start_addnum;
				this.stream_params = this._bind_stream_params(this.stream_date, this.stream_start);
				api.get_stream(this, this.stream_params).then((result) => {
					this.busy = false;
					if (!result.cards || result.cards.lenght < 16) {
						// 没有数据不允许继续加载
						this.loadend = true;
						this.busy = true;
						// setTimeout(() => {
						// 	this.loadend = false;
						// }, 1000)
					} else {
						this.stream_list = this.stream_list.concat(result['cards'].slice(0, 16));
						this.first_log = true;
					}
				})
			},
			_bind_stream_params: function(date, start) {
				return `infoline/openapi?date=${date}&client=external&start=${start}`;
			}
		},
		mounted() {
			// 请求参数
			this.stream_date = util.formatTime(new Date());
			this.stream_params = this._bind_stream_params(this.stream_date, this.stream_start);
			api.get_stream(this, this.stream_params).then((result) => {
				this.stream_list = result['cards'].slice(0, 16);
			})
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.stream-container {
		transition: 1s;
	}
	.stream-loadend {
		font-size: .16rem;
		padding: .133333rem 0 0 0;
	}
	.stream-ad {
		// border-bottom: .013333rem solid #B9BCC2;
		padding: 0 .28rem 0 .213333rem;
		position: relative;
	}
	.stream-ad:before, .stream-content:before {
        content: "";
        pointer-events: none; /* 防止点击触发 */
        box-sizing: border-box;
        position: absolute;
        width: 100%;
        height: 200%;
        left: 0;
        top: 0;
        border-bottom: 1px #e6e6e6 solid;
        transform: scaleY(0.5);
        transform-origin: 0 0;
    }
	.stream-content {
		// border-bottom: .013333rem solid #B9BCC2;
		// padding: 0 .426667rem 0 .32rem;
		padding: 0 .28rem 0 .213333rem;
		position: relative;
		// border-bottom: .013333rem #e6e6e6 solid;
	}
</style>



