<template>
	<div class='loading-content-container'>
        <div class='loading-content-title'>
            正在刷新数据中...
        </div>
        <div class='loading-content-time'>
            最后更新: {{last_update_time}}
        </div>
    </div>
</template>

<script>
export default {
	data () {
		return {
            last_update_time: ''
		}
	},
    methods: {
        _bind_return_msg(last, cur) {
            let curTimeArr = cur.split('/');
            let lastTimeArr = last.split('/');
            let gap = curTimeArr[2] - lastTimeArr[2];
            let result;
            switch(gap) {
                case 0: 
                    result = `今天 ${lastTimeArr[3]}:${lastTimeArr[4]}`;
                    break;
                case 1:
                    result = `昨天 ${lastTimeArr[3]}:${lastTimeArr[4]}`;
                    break;
                case 2:
                    result = `前天 ${lastTimeArr[3]}:${lastTimeArr[4]}`;
                    break;
                default:
                    result = `很久之前`;
                    break;
            }
            return result;
        }
    },
	mounted() {
        // 目前的时间
        let currentTime = new Date();
        let currentYear = currentTime.getFullYear();
        let currentMonth = currentTime.getMonth();
        let currentDay = currentTime.getDate();
        let currentHour = currentTime.getHours();
        let currentMin = currentTime.getMinutes();
        let cur = `${currentYear}/${currentMonth}/${currentDay}/${currentHour}/${currentMin}`;
        // 形式是2017/12/11/12/44
        let lastTime = localStorage.getItem('lastTime');
        if (!lastTime) {
            this.last_update_time = `今天 ${currentHour}:${currentMin}`
        } else {
            this.last_update_time = this._bind_return_msg(lastTime, cur);
        }
        localStorage.setItem('lastTime', cur);
	}  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.loading-content-container {
        font-size: .16rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        // padding: .133333rem;
        padding: .066667rem;
    }
    .loading-content-title {
        // padding: .066667rem;
        padding: .04rem;
    }
    .loading-content-time {
        // padding: .133333rem;
        padding: .066667rem;
    }
</style>


