<template>
    <div class='art-container'>
        <StreamLodingAnimation v-show='!loaded'/>
        <div class='art-content-container'
            v-show='loaded'>
            <div class='art-content-title'>
                {{this.article.title}}
                <span v-if='!article.pid'>{{this.artBkTitle}}</span>
            </div>
            <StreamAudioPlayer
                :class='audio_item.audio_index'
                :url='audio_item.audio_url'
                :title='audio_item.audio_title'
                :pic='audio_item.audio_pic'
                :key='audio_item.audio_url'
                v-for='(audio_item, index) in audio_list'/>
            <StreamVideoPlayer  
                :class='video_item.video_index'
                :src='video_item.video_url'
                :key='video_item.video_url'
                v-for='(video_item, index) in video_list' />
            <div class='art-video-wrapper' 
                v-for='(video_item, index) in video_html_list'
                :class='video_item.video_index'
                :key='video_item.video_html'
                v-html='video_item.video_html' v-show='false'>
            </div>
            <div class='art-content-desc-bk'>
                <span v-if='!article.pid && !timeout'>文章不存在</span>
                <span v-if='!article.pid && timeout'>
                    <div>网络超时</div>
                    <span class='art-content-desc-bk-reload' @touchstart='_bind_reset'>重新加载</span>
                </span>
            </div> 
            <div class='art-content-desc' v-html='this.article.content'>
            </div> 
            <div class='art-content-recommendlist' v-show='recommend_list.length>0'>
                <div class='art-content-recommendheader'>
                    <span>相关阅读</span>
                </div>
                <div v-for='(recommend_item, index) in recommend_list'>
                    <StreamContentItem 
                        :item='recommend_item'
                        :pure='true'
                        :norlog='true'/>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import Vue from 'vue';
    import StreamContentItem from './StreamContentItem';
    import StreamAudioPlayer from './StreamAudioPlayer';
    import StreamLodingAnimation from './StreamLodingAnimation';
    import StreamVideoPlayer from './StreamVideoPlayer';
    import util from '../util/util.js';
    import api from '../service/apihelper.js';
    export default {
        computed: {
            // 文章id
            artUrlId: function() {
                return this.$route.query.urlId;
            },
            artBkTitle: function() {
                return this.$route.query.title;
            },
            // 推荐列表
            recommend_list: function() {
                let list = [];
                if (this.article.related) {
                    this.article.related.forEach((item) => {
                        list.push({
                            id: item.id,
                            title: item.title,
                            summary: item.summary,
                            image: item.image,
                            url: item.url
                        })
                    })
                }
                return list;
            }
        },
        watch: {
            '$route': ['_bind_reset']
        },
        components: {
            StreamAudioPlayer,
            StreamContentItem,
            StreamLodingAnimation,
            StreamVideoPlayer
        },
        data () {
            return {
                // 超时标志
                timeout: false,
                // 请求querystring
                apiOption: { showmethod: 'native', keyfrom: 'wap', client: 'wap', callback: 'jsonp_article' },
                // 文章请求参数
                art_params: '',
                // 文章日期
                article_date: '',
                // 文章信息
                article: {},
                // 是否加载文章完成
                loaded: false,
                // 视频信息
                video_list: [],
                video_html_list: [],
                // 模拟插入的video_url html
                video_loaded: false,
                video_html_loaded: false,
                // 是否图片加载完成
                img_loaded: false,
                // 音频信息
                audio_loaded: false,
                audio_list: []
            }
        },
        methods: {
            // 重置参数
            _bind_reset() {
                // 根据路由变化重新刷新页面
                location.reload();
            },
            // 组装参数
            _bind_get_params(pre, option) {
                let queryStringList = [];
                for (let i in option) {
                    queryStringList.push(`${i}=${option[i]}`);
                }
                return `${pre}?${queryStringList.join('&')}`;
            },
            // log记录
            bind_rlog_pv() {
                window._rlog.push(["_addPost" , "infoid" , `${this.article.cardInfo && this.article.cardInfo.id}`]);
                window._rlog.push(["_addPost" , "style" , `${this.article.style && this.article.style.style}`]);
                window._rlog.push(["_addPost" , "url" , `${location.href}`]);
                window._rlog.push(["_addPost" , "date" , `${this.article_date}`]);
                window._rlog.push(["_trackEvent", `stream_sw_page_view`]);
            },
            // 请求文章
            bind_get_article() {
                // 回到顶部
                window.scrollTo(0, 0);
                this.article_date = util.formatTime(new Date());
                this.art_params = this._bind_get_params(`sw/m/${this.artUrlId}`, this.apiOption); 
                api.get_article(this, this.art_params).then((result) => {
                    // 超时处理
                    if (result.status == 408) {
                        this.timeout = true;
                        this.loaded = true;
                    } else {
                        let art_view_add_link = this._bind_get_params('sw/updatevisits', { pid: result.pid});
                        this.loaded = true;
                        this.article = result;
                        api.visit_article(this, art_view_add_link);
                        // log
                        this.bind_rlog_pv();
                    }
                })
            }
        },
        mounted() {
            window.onbeforeunload = function() {
                window.scrollTo(0, 0);
            }
            // 获取文章内容
            this.bind_get_article(this.art_params);
        },
        updated: function () {
            if (this.article.content) {
                this.$nextTick(function () {
                    // 把video插入指定位置
                    if (this.video_html_loaded && this.video_loaded) {
                        this.video_list.forEach((item) => {
                            let video_container = document.querySelector(`.video-play-btn[index="${item.video_index}"]`);
                            let video_item = document.querySelector(`.video-content-wrapper.${item.video_index}`);
                            video_container.appendChild(video_item);
                        })
                    }
                    // video已经插入页面
                    if (this.video_html_loaded && !this.video_loaded) {
                        let video_real_list = document.querySelectorAll('.art-video-wrapper video');
                        if (video_real_list.length > 0) {
                            [].forEach.call(video_real_list, (video, index) => {
                                let video_src = video.getAttribute('src');
                                let video_poster = video.getAttribute('poster');
                                this.video_list.push({
                                    video_url: video_src,
                                    video_poster: video_poster,
                                    video_index: `index${index}`
                                })
                            })
                        }
                        this.video_loaded = true;
                    }
                    // 获取页面的video
                    if (!this.video_html_loaded) {
                        let video_html_list = document.querySelectorAll('.video-play-btn');
                        if (video_html_list.length > 0) {
                            [].forEach.call(video_html_list, (video, index) => {
                                let video_html_real= document.querySelector('script[type="text/video-template"]');
                                video.setAttribute('index', `index${index}`);
                                let video_html = video_html_real.innerHTML;
                                this.video_html_list.push({
                                    video_html: video_html
                                })
                            })
                        }
                        this.video_html_loaded = true;
                    }
                    // 把audio加入指定位置
                    if (this.audio_loaded) {
                        // 现在audio html已经加入了
                        this.audio_list.forEach((item) => {
                            let audio_container = document.querySelector(`.audio-play-btn[index="${item.audio_index}"]`);
                            let audio_item = document.querySelector(`.audio-content-wrapper.${item.audio_index}`);
                            audio_container.appendChild(audio_item);
                        })
                    }
                    // 获取页面的audio
                    if (!this.audio_loaded) {
                        let audio_real_list = document.querySelectorAll('.audio-play-btn');
                        if (audio_real_list.length > 0) {
                            let title = this.article.title;
                            [].forEach.call(audio_real_list, (audio, index) => {
                                audio.setAttribute('index', `index${index}`);
                                this.audio_list.push({
                                    audio_url: audio.getAttribute('src'),
                                    audio_pic: audio.getAttribute('poster'),
                                    audio_title: title,
                                    audio_index: `index${index}`
                                })
                            })
                        }
                        this.audio_loaded = true;
                    }
                    // 替换页面的img
                    if (!this.img_loaded) {
                        // 替换img图片
                        let imgList = document.querySelectorAll('.art-content-desc img');
                        [].forEach.call(imgList, function(img) {
                            let real_url = img.getAttribute('dataimg');
                            real_url && img.setAttribute('src', real_url);
                        })  
                        this.img_loaded = true;
                    }
                })
            }
        }
    }
</script>
<style>
    .art-container {
        height: 100%;
    }
    .art-content-container {
        display: flex;
        flex-direction: column;
        // justify-content: center;
        padding: .186667rem .2rem;
        // font-size: .4rem;
        font-size: .2rem;
        min-height: 100%;
    }
    .art-content-title {
        // font-family: PingFangSC-Medium;
        text-align: left;
        font-size: .293333rem;
        color: #333333;
        // padding-bottom: .426667rem;
        padding-bottom: .213333rem;
        line-height: .44rem;
        width: 100%;
        // border-bottom: 1px #E0E0E0 dotted;
        position: relative;
        font-weight: 700;
    }
    .art-content-title:before {
        content: "";
        pointer-events: none; /* 防止点击触发 */
        box-sizing: border-box;
        position: absolute;
        width: 100%;
        height: 200%;
        left: 0;
        top: 0;
        border-bottom: 1px #d6d6d6 dotted;
        transform: scaleY(0.5);
        transform-origin: 0 0;
    }
    .art-content-desc, .art-content-desc-bk {
        text-align: justify;
        color: #666;
        // line-height: .373333rem;
        line-height: 1.8;
        font-size: .213333rem;
        letter-spacing: 0;
        word-break: normal;
        word-wrap: break-word;
    }
    .art-content-desc-bk {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1;
        text-align: center;
    }
    .art-content-desc-bk-reload {
        background: #61ACF8;
        color: #fff;
        padding: .066667rem;
        font-size: .16rem;
    }
    .art-content-desc blockquote {
        color: #999;
        // font-size: .373333rem;
        font-size: .186667rem;
        // margin: .666667rem auto;
        margin: .333333rem auto;
        // padding-left: .213333rem;
        padding-left: .106667rem;
        // border-left: .106667rem solid #ebebeb;
        border-left: .053333rem solid #ebebeb;
        font-family: "Helvetica Neue",Helvetica,sans-serif;
    }
    .art-content-desc blockquote p {
        line-height: 1.5;
    }
    .art-content-desc img {
        width: 100%;
        // margin: .4rem 0;
        margin: .2rem 0;
        display: block;
    }
    .art-content-desc .video-play-btn img {
        display: none;
    }
    .art-content-desc .audio-play-btn {
        font-size: 0;
        line-height: 0;
        -webkit-user-select:none;
        -moz-user-select:none;
        -ms-user-select:none;
        user-select:none;
        -webkit-tap-highlight-color: transparent;
         -webkit-touch-callout: none;
        cursor: none;
    }
    .art-content-desc p {
        // margin: .426667rem 0;
        margin: .213333rem 0;
    }
    .art-content-audio {
        // padding: .32rem;
        padding: .16rem;
    }
    .art-content-imglist {
        // margin-top: 1.4rem;
        margin-top: .7rem;
        display: flex;
        flex-direction: column;
    }
    .art-content-imglist img {
        // height: 5.333333rem;
        height: 2.666667rem;
        // margin: .2rem 0;
        margin: .1rem 0;
    }
    .art-content-recommendlist {
        // margin-top: .2rem;
        margin-top: .1rem;
        text-align: left;
    }
    .art-content-recommendheader {
        // height: .8rem;
        height: .4rem;
        // line-height: .8rem;
        line-height: .4rem;
        // border-bottom: .04rem solid #EFEFEF;
        border-bottom: .02rem solid #EFEFEF;
    }
    .art-content-recommendheader span {
        display: inline-block;
        // height: .8rem;
        height: .4rem;
        // border-bottom: .04rem solid #DB1920;
        border-bottom: .02rem solid #DB1920;
    }
</style>


