const get_article = function(context, params) {
    return context.$http.jsonp(`${httpAds.STREAM_ART_ADS}/${params}`, {_timeout: 10000})
        .then(resp=>{  
            return resp.data;
        },err => {  
            return err;  
        }).catch(() => {
            return {};
        })
        
}
const visit_article = function(context, params) {
    return axios.get(`${httpAds.STREAM_ART_ADS}/${params}`)
        .then((res) => {
            return res.data;
        }).catch(() => {
            return {};
        })
}
export default { get_stream, get_article, visit_article };


